/* raid.css */


.raid-container {

  position: relative;
    background: linear-gradient(to bottom right, #0f0f0f, #1c1c1c);
      min-height: 100vh;
    color: white;
    margin: 0px;
    box-sizing: border-box;
}

.form-toggle {
  position: fixed;
  bottom: 0.75rem;
  right: 1rem;
  background: #444;
  color: white;
  padding: 6px 10px;
  border-radius: 8px;
  font-size: 1.2rem;
  cursor: pointer;
  z-index: 1001;
  user-select: none;
  transition: transform 0.3s ease;
}



/* Yellow when form is hidden */
.form-toggle.closed {
  color: #333;
  background-color: yellow;
  transform: rotate(0deg);
}

/* Rotate when form is open */
.form-toggle.open {
  color: white;
  transform: rotate(180deg);
}


.sticky-form {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
padding: 10px;
  background: rgba(0, 0, 0, 0.699);
    backdrop-filter: blur(10px);
  border-top: 1px solid #333;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  z-index: 1000;
}

.raid-header {
  text-align: center;
  margin-bottom: 2rem;
}

.raid-header h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  color: #ffd700;
}

.raid-header p {
  color: #aaa;
  font-size: 1rem;
}

.raid-form {

    width: 100%;
    box-sizing: border-box;
}

.formcontainer {
    max-width: 950px !important;
        width: 100%;
      display: flex;
  flex-direction: column;
  
  margin: 0 auto 0rem auto;
  gap: 8px;
  box-sizing: border-box;
}

.raid-form input {
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.733);
  color: #1f1f1f;
}

.raid-form button {
  padding: 0.75rem;
  background-color: #ffd700;
  color: #000;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease;
}

.raid-form button:hover {
  background-color: #e6c200;
}

.raid-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
  max-width: 900px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  padding-bottom: 60px;
  z-index: 1000 !important;
}

.raid-card {
  background: rgba(0, 0, 0, 0.699);
    backdrop-filter: blur(10px);
  border: 1px solid #333;
  padding: 1rem;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  position: relative;
    z-index: 1000 !important;
}

.raid-card h3 {
  margin: 0;
  font-size: 1.25rem;
  color: #fff;
}

.raid-card a {
  color: #1da1f2;
  text-decoration: none;
  word-break: break-all;
}

.raid-time {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 0.75rem;
  color: #999;
}

.raid-actions {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  
}

.raid-actions button {
  flex: 1;
  background: #2a2a2a;
  color: #fff;
  border: 1px solid #444;
  padding: 0.5rem;
  border-radius: 6px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.raid-actions button:hover {
  background: #3a3a3a;
  border-color: #666;
}

.raid-actions button.copy-success {
  background: #0f0;
  color: #000;
}


.raidbutton {
  font-weight: bold;
  color: white !important;
  background: black;
  padding: 12px 24px;
  border: 4px solid transparent;
  border-radius: 8px;
  position: relative;
  z-index: 1;
  cursor: pointer;
  overflow: hidden;
}

.raidbutton::before {
  content: "";
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  background: linear-gradient(
    60deg,
    red,
    orange,
    yellow,
    green,
    blue,
    indigo,
    violet,
    red
  );
  background-size: 300% 300%;
  animation: borderAnim 4s linear infinite;
  z-index: -1;
  border-radius: 12px;
}

@keyframes borderAnim {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}


.headerraid{
    display: flex;
    align-items: center;
        padding: 20px;
        justify-content: center;
        z-index: 1000 !important;
}

.raid-title {
    margin: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
           z-index: 1000 !important;
}


.booster {
    white-space: nowrap;
}


